<template>
  <div>
    <Header />
    <SubNav />
    <router-view :key="key" />
  </div>
</template>
<script>
import Header from "@/components/common/Header";
import SubNav from "@/components/common/SubNav";

export default {
  name: "MainLayout",
  components: {
    Header,
    SubNav,
  },
  computed: {
    key() {
      return this.$route.path;
    },
  },
};
</script>
