export const upload = async function (params, callback1, callback2) {
  if (params.Key != null) {
    params.Key = storageInfo.root_folder + params.Key;
  }
  let requestParams = {
    ...params,
    Bucket: storageInfo.bucket_name,
    "Access-Control-Allow-Credentials": "*",
    ACL: "public-read",
  };
  await S3.upload(requestParams, {}, callback1).on(
    "httpUploadProgress",
    callback2
  );
};

export const deleteFile = async function (object_name) {
  (async () => {
    // Delete File
    await S3.deleteObject({
      Bucket: storageInfo.bucket_name,
      Key: object_name,
    }).promise();
  })();
};

export const putBucketCors = async function () {
  let params = {
    Bucket: "ubidtx01",
    CORSConfiguration: {
      CORSRules: [
        {
          AllowedHeaders: ["*"],
          AllowedMethods: ["HEAD", "GET", "PUT", "POST", "DELETE"],
          AllowedOrigins: ["*"],
          ExposeHeaders: ["ETag", "x-amz-meta-custom-header"],
          MaxAgeSeconds: 3000,
        },
      ],
    },
    ContentMD5: "",
  };
  S3.putBucketCors(params, function (err, data) {
    if (err) console.log(err, err.stack);
    // an error occurred
    else console.log(data); // successful response
  });
};
