import Vue from "vue";
import Vuex from "vuex";
import sha256 from "sha256";
// 테스트 데이터
import pageInfo from "@/data/pageInfo";
import patientList from "@/data/patientList";
import prescriptionTemplate from "@/data/prescriptionTemplate";
import treatmentStatus from "@/data/treatmentStatus";
import treatmentList from "@/data/treatmentList";
import treatmentManagement from "@/data/treatmentManagement";
import systemManagement from "@/data/systemManagement";
import prescription from "@/data/prescriptionData";
// // 테스트 데이터
import {
  setToken,
  removeToken,
  setSimpleUserInfo,
  removeSimpleUserInfo,
  getSimpleUserInfo,
} from "@/utill/auth";
import {
  login,
  // getInfo,
  logout,
} from "@/api/user";
import { getFunctionsByRole } from "@/api/role";
import { getMenuList } from "@/api/menu";
import _ from "lodash";
import { transformToMenuTree } from "@/utill/commom";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 테스트 데이터
    patientList: patientList,
    treatmentStatusList: treatmentStatus,
    treatmentList: treatmentList,
    treatmentManagement: treatmentManagement,
    prescriptionTemplate: prescriptionTemplate,
    systemManagement: systemManagement,
    prescription: prescription,
    // // 테스트 데이터
    pageInfo: pageInfo,
    role: "",
    loginYN: false,
    userInfo: "",
    userFunctions: [],
    userPassword: "",
    userMenus: [],
    userMenuTree: [],
  },
  getters: {
    getElapsedDaysTop5: function (state) {
      return state.patientList.sort(function (a, b) {
        if (a.elapsedDays < b.elapsedDays) return 1;
        if (a.elapsedDays > b.elapsedDays) return -1;
        return 0;
      });
    },
    getRole: function (state) {
      return state.role;
    },
    getUserInfo: function (state) {
      return state.userInfo;
    },
    getUserFunctions: function (state) {
      return state.userFunctions;
    },
    getUserMenuTree: function (state) {
      return state.userMenuTree;
    },
    getUserMenus: function (state) {
      return state.userMenus;
    },
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token;
    },
    SET_USERINFO: (state, userInfo) => {
      state.userInfo = userInfo;
    },
    SET_ROLE: (state, role) => {
      state.role = role;
    },
    SET_USER_FUNCTIONS: (state, functions) => {
      state.userFunctions = functions;
    },
    SET_USER_MENU_TREE: (state, menus) => {
      state.userMenuTree = menus;
    },
    SET_USER_MENUS: (state, menus) => {
      state.userMenus = menus;
    },
  },
  actions: {
    login({ commit }, userInfo) {
      return new Promise((resolve, reject) => {
        login(userInfo)
          .then((response) => {
            const data = response.data.response;
            console.log(data);
            commit("SET_USERINFO", data);
            commit("SET_ROLE", data.roleId);
            data.userId = userInfo.userId;
            data.userPassword = sha256(userInfo.password);
            setSimpleUserInfo(data);
            setToken(data.accessToken);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    logout({ commit }) {
      return new Promise((resolve, reject) => {
        logout()
          .then(() => {
            commit("SET_USERINFO", "");
            commit("SET_ROLE", "");
            commit("SET_USER_MENUS", "");
            commit("SET_USER_MENU_TREE", "");
            removeSimpleUserInfo();
            removeToken();
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    // getUserInfo({ commit }) {
    //   return new Promise((resolve, reject) => {
    //     console.log(`try to call getInfo api`);
    //     getInfo()
    //       .then((response) => {
    //         const data = response.data.response;
    //         commit("SET_USERINFO", data);
    //         //commit("SET_ROLE", data);
    //         resolve(data);
    //       })
    //       .catch((error) => {
    //         reject(error);
    //       });
    //   });
    // },
    // remove token
    resetToken({ commit }) {
      console.log(`try to reset token`);
      return new Promise((resolve) => {
        commit("SET_TOKEN", "");
        commit("SET_ROLE", "");
        removeToken();
        resolve();
      });
    },
    getUserFunctions({ commit }) {
      return new Promise((resolve, reject) => {
        let userInfo = getSimpleUserInfo();
        if (userInfo) {
          console.log(`try to call getUserFunctions api`);
          getFunctionsByRole(JSON.parse(userInfo).roleId)
            .then((response) => {
              const data = response.data.response.data;
              commit("SET_USER_FUNCTIONS", data);
              resolve(data);
            })
            .catch((error) => {
              reject(error);
            });
        }
      });
    },
    createUserMenuTree({ commit }) {
      return new Promise((resolve, reject) => {
        let userInfo = getSimpleUserInfo();
        let userFunctions = [];
        let allMenus = [];
        if (userInfo) {
          console.log(`try to call getUserFunctions api`);
          getFunctionsByRole(JSON.parse(userInfo).roleId)
            .then((response) => {
              userFunctions = response.data.response.data;
            })
            .then(() => {
              getMenuList().then((response) => {
                let menuList = response.data.response.data;
                menuList = menuList.filter(
                  (obj) => obj.funcNm !== "앱 로그 조회"
                ); // 앱 로그 조회 임시 필터링 22.09.02
                // 삭제된 메뉴 Header에 표시하지 않도록 처리 22.09.15
                menuList = menuList.filter((obj) => obj.useYn !== "N");
                menuList.forEach(function (menu) {
                  let isActive = _.findIndex(userFunctions, function (o) {
                    return o.funcId == menu.funcId && o.useYn == "Y";
                  });
                  if (isActive > -1) {
                    allMenus.push(menu);
                  }
                });
                let parents = [];
                let children = [];
                allMenus.forEach(function (menu) {
                  menu.subMenu = [];
                  if (menu.menuHighId === "") {
                    parents.push(menu);
                  }
                });
                children = allMenus.filter(function (menu) {
                  return menu.menuHighId !== "";
                });
                transformToMenuTree(parents, children);
                console.log(parents);
                commit("SET_USER_MENU_TREE", parents);
                resolve(parents);
              });
            })
            .catch((error) => {
              reject(error);
            });
        }
      });
    },
    createUserMenu({ commit }) {
      return new Promise((resolve, reject) => {
        let userInfo = getSimpleUserInfo();
        let userFunctions = [];
        let allMenus = [];
        if (userInfo) {
          getFunctionsByRole(JSON.parse(userInfo).roleId)
            .then((response) => {
              userFunctions = response.data.response.data;
            })
            .then(() => {
              getMenuList().then((response) => {
                let menuList = response.data.response.data;
                menuList.forEach(function (menu) {
                  let isActive = _.findIndex(userFunctions, function (o) {
                    return o.funcId == menu.funcId && o.useYn == "Y";
                  });
                  if (isActive > -1) {
                    allMenus.push(menu);
                  }
                });
                console.log(allMenus);
                commit("SET_USER_MENUS", allMenus);
                resolve(allMenus);
              });
            })
            .catch((error) => {
              reject(error);
            });
        }
      });
    },
  },
  modules: {},
});
