import Cookies from "js-cookie";

const TokenKey = "User-Token";
const SimpleUserInfo = "User-Info";

export function getToken() {
  return Cookies.get(TokenKey);
}

export function setToken(token) {
  return Cookies.set(TokenKey, token);
}

export function removeToken() {
  return Cookies.remove(TokenKey);
}

export function getSimpleUserInfo() {
  return {
    accessToken: "a9c0111b-32a6-4328-9250-76cac3c07a17",
    name: "테스트유저",
    roleId: "ROLE001",
    roleName: "시스템관리자",
    centerId: "CNTR002",
    centerName: "기타 센터",
    deptId: "DEPT001",
    deptName: "신경과",
    userId: "test",
    userPassword: "1234",
  };
}

export function setSimpleUserInfo(userInfo) {
  return window.localStorage.setItem(SimpleUserInfo, JSON.stringify(userInfo));
}

export function removeSimpleUserInfo() {
  return window.localStorage.setItem(SimpleUserInfo, "");
}
